.option>p{
  color: white;
  font-size: 50px;
  text-shadow: 3px 3px red;
  opacity: 100%;

  background-size: cover;

}

h1 { font-family: "museo-sans-rounded-bold, sans-serif"; font-size: 40px; font-style: normal; font-variant: normal; font-weight: 800; line-height: 26.4px; margin: 30px; } 

.light{
  color: black;
}

.dark{
  color: white;
}


.option:hover {
  transform: scale(1.01);
  transition: transform 0.5s ease, box-shadow 0.5s ease;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.6);
}



.metrics {
  display: flex; /* Arrange metrics horizontally */
  justify-content: space-between; /* Distribute evenly */
  margin-top: 20px; /* Add spacing */
}

.metric {
  width: 33%; /* Equal width for each metric */
  text-align: center;
}

.metric .value {
  font-size: 20px; /* Adjust font size as needed */
  font-weight: bold;
}

.metric .label {
  font-size: 14px; /* Adjust font size as needed */
  color: #888; /* Lighter text for label */
}







