.loader {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: black;
    color: white;
    font-size: 40px; /* Double the initial size */
    z-index: 1000;
  }
  
  .text {
    animation: fadeIn 0.5s ease-out;
  }
  
  .tick {
    font-size: 40px; /* Ensure the tick matches the increased text size */
    margin-left: 10px;
    animation: stamp 0.5s ease-out forwards; /* Stamp-like appearance */
  }
  
  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  
  @keyframes stamp {
    from {
      transform: scale(0);
    }
    to {
      transform: scale(1);
    }
  }
  
  .loading-animation {
    width: 50px;
    height: 50px;
    border: 5px solid #f3f3f3;
    border-radius: 50%;
    border-top: 5px solid #444;
    margin-top: 20px;
    animation: spin 1s linear infinite;
  }
  
  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  